import Vue from 'vue'
import VueRouter from 'vue-router'

const Login = () => import(/* webpackChunkName: "login_home_welcome" */ '../components/Login.vue')
const Home = () => import(/* webpackChunkName: "login_home_welcome" */ '../components/Home.vue')
const Welcome = () => import(/* webpackChunkName: "login_home_welcome" */ '../components/Welcome.vue')

const Staffs = () => import(/* webpackChunkName: "users" */ '../components/User/Staffs.vue')
const Customers = () => import(/* webpackChunkName: "users" */ '../components/User/Customers.vue')

const Cate = () => import(/* webpackChunkName: "cate_data" */ '../components/Category/Cate.vue')
const Report = () => import(/* webpackChunkName: "cate_data" */ '../components/Data/Report.vue')

const Goods = () => import(/* webpackChunkName: "goods" */ '../components/Goods/List.vue')
const AddGoods = () => import(/* webpackChunkName: "goods" */ '../components/Goods/Add.vue')
const EditGoods = () => import(/* webpackChunkName: "goods" */ '../components/Goods/Edit.vue')

if (process.env.NODE_ENV === 'development') {
  Vue.use(VueRouter)
}

const routes = [{
  path: '/',
  redirect: '/login'
}, {
  path: '/login',
  component: Login
}, {
  path: '/home',
  redirect: '/welcome',
  component: Home,
  children: [
    { path: '/welcome', component: Welcome },
    { path: '/staffs', component: Staffs },
    { path: '/customers', component: Customers },
    { path: '/categories', component: Cate },
    { path: '/goods', component: Goods },
    { path: '/goods/add', component: AddGoods },
    { path: '/goods/edit/:gid', component: EditGoods },
    { path: '/reports', component: Report }
  ]
}]

const router = new VueRouter({
  routes
})

// mount a nav guard
const jwt = require('jsonwebtoken')
router.beforeEach((to, from, next) => {
  var tokenStr = window.sessionStorage.getItem('token')
  if (to.path === '/login') {
    // check if this user has logined
    if (!tokenStr) return next()
    // verify token
    jwt.verify(tokenStr, 'secretkey', function(err, decoded) {
      if (err) return next()
      return next('/home')
    })
  }

  if (!tokenStr) return next('/login')
  // verify token
  jwt.verify(tokenStr, 'secretkey', function(err, decoded) {
    if (err) return next('/login')
    return next()
  })
})

export default router
